import styled from '@emotion/styled'
import { motion } from "framer-motion";
import TextSpan from './TextSpan';

const TextList = [
  '  ,          .',
  '    .',
  '          .',
  '  ,         .',
  '  2~3     .',
  '           .'
]

const ScrollContent = () => {
  return(
    <ScrollContainer>
      {
        TextList.map(text => {
          return(
            <TextSpan key={text}>
              {text}
            </TextSpan>
          )
        })
      }
    </ScrollContainer>
  )
};

const ScrollContainer = styled(motion.div)`
  max-width: 50%;
  margin: 0 auto;
  height: 200px;
  position: relative;
  z-index: 1000;
  text-align: center;
  font-size: 52px;
  font-weight: 700;
  letter-spacing: -1.5px;
`;

export default ScrollContent;